<template>
	<article>
		<v-slider sliderFlag="sliderFlag_text" :items="menuValArr" :options="options" :class="$style.sliderText"></v-slider>
	</article>
</template>

<script>
import slider from '../core/slider.vue'
export default {

  name: 'menuslider',
  components:{
  	'v-slider':slider
  },
  props: {
    menuType: {
        type: String,
        default: ''
    }
  },
  data () {
    return {
        options: {
            slidesPerView : 3,
            centeredSlides : false,
            slidesPerView :'auto'
        },
        menuMap: {
            'meishi':['','','','','','','','',''],
            'wancan':['','','',''],
            'shangchaobianli':['','','',''],
            'shuiguo':['','','','','',''],
            'yiyaojiankang':['','','','','',''],
            'langmanxianhua':['','','','',''],
            'chufangshengxian':['','','','','','',''],
            'paotuidaigou':['','',''],
            'difangxiaochi':['','','','',''],
            'malangtang':['','','',''],
            'difangcaixi':['','','','','','','','','',''],
        },
        keyword: '',
        iconP: '\ue86f ',
    }
  },
  computed: {
    menuValArr: function(){
        return this.menuMap[this.menuType]
    }
  }
}
</script>

<style lang="scss" module>
@import "~@/css/element.scss";
.sliderText{
